"use client";

import { useState, useMemo, useEffect } from 'react';
import Image from 'next/image';

const PRODUCTS = [
    {
        id: 1,
        name: "VALENCIA ORANGE MOCHA",
        price: "$7.50",
        category: "Signature",
        description: "A bold fusion of dark cocoa and zesty Valencia oranges, topped with crystalized zest.",
        image: "/Drizzle4.png",
        tags: ["Zesty", "Dark Cocoa", "Hot"]
    },
    {
        id: 2,
        name: "KYOTO COLD DRIP",
        price: "$8.00",
        category: "Cold Brew",
        description: "Slow-dripped over 12 hours through glass towers to extract pure, floral clarity.",
        image: "/DrizzlCafe1.jpeg",
        tags: ["Floral", "Clear", "Drip"]
    },
    {
        id: 3,
        name: "BLACK GOLD ESPRESSO",
        price: "$4.50",
        category: "Strong",
        description: "Our darkest roast. Intense complexity with notes of smoke and black treacle.",
        image: "/DrizzlCafe2.jpeg",
        tags: ["Intense", "Smoky", "Short"]
    },
    {
        id: 4,
        name: "VELVET FLAT WHITE",
        price: "$5.95",
        category: "Classics",
        description: "Micro-foamed milk poured over a precision-pulled double shot of our house blend.",
        image: "/Drizzl3Cafe3.jpeg",
        tags: ["Creamy", "Balanced", "Classic"]
    },
    {
        id: 5,
        name: "TURKISH ROSE LATTE",
        price: "$6.50",
        category: "Signature",
        description: "Delicate rose water infusion with crushed pistachios and honey-drizzled foam.",
        image: "/DrizzlCafe5.jpeg",
        tags: ["Floral", "Sweet", "Exotic"]
    },
    {
        id: 6,
        name: "HONEYCOMB NITRO",
        price: "$7.25",
        category: "Cold Brew",
        description: "Nitro-infused cold brew served with a shard of handmade honeycomb toffee.",
        image: "/Drizzle6.png",
        tags: ["Creamy", "Sweet", "Draft"]
    }
];

const CATEGORIES = ["All", "Signature", "Classics", "Cold Brew", "Strong"];

export default function ProductsPage() {
    const [selectedCategory, setSelectedCategory] = useState("All");
    const [isLoaded, setIsLoaded] = useState(false);

    useEffect(() => {
        setIsLoaded(true);
    }, []);

    const filteredProducts = useMemo(() => {
        return selectedCategory === "All"
            ? PRODUCTS
            : PRODUCTS.filter(p => p.category === selectedCategory);
    }, [selectedCategory]);

    return (
        <div className="min-h-screen bg-white">

            <div className="relative z-10 flex flex-col lg:flex-row min-h-screen pt-24">

                {/* Left Sidebar - Fixed on Desktop */}
                <aside className="lg:w-96 lg:h-[calc(100vh-6rem)] lg:sticky lg:top-24 pt-20 pb-12 px-12 flex flex-col justify-between border-r border-gold/10 bg-cream/30 backdrop-blur-sm">
                    <div className="space-y-16">
                        <div className="space-y-4">
                            <span className="text-gold text-[10px] font-bold tracking-[0.4em] uppercase">Selection</span>
                            <h2 className="font-serif text-5xl text-charcoal leading-[0.9] tracking-tighter">CURATED<br />ROASTS</h2>
                        </div>

                        <nav className="flex flex-row lg:flex-col gap-8 overflow-x-auto pb-4 lg:pb-0 lg:overflow-visible">
                            {CATEGORIES.map((cat) => (
                                <button
                                    key={cat}
                                    onClick={() => setSelectedCategory(cat)}
                                    className={`text-left group flex items-center gap-6 transition-all duration-500 shrink-0 ${selectedCategory === cat ? 'lg:translate-x-4' : 'lg:hover:translate-x-2'
                                        }`}
                                >
                                    <span className={`hidden lg:block h-[1px] transition-all duration-500 bg-gold ${selectedCategory === cat ? 'w-12' : 'w-0 group-hover:w-8'
                                        }`}></span>
                                    <span className={`text-[11px] font-bold tracking-[0.2em] uppercase transition-colors ${selectedCategory === cat ? 'text-gold' : 'text-charcoal/40 group-hover:text-charcoal'
                                        }`}>
                                        {cat}
                                    </span>
                                </button>
                            ))}
                        </nav>
                    </div>

                    <div className="hidden lg:block space-y-4 text-[10px] text-charcoal/30 tracking-[0.3em] font-bold uppercase">
                        <p>Â© 2026 BARISTA COFFEE</p>
                        <p>PREMIUM CRAFTED</p>
                    </div>
                </aside>

                {/* Main Content Area */}
                <div className="flex-1 pt-20 px-8 md:px-16 lg:px-24 pb-40">

                    {/* Page Header */}
                    <div className="mb-32 space-y-10 max-w-5xl">
                        <div className="flex items-center gap-6">
                            <div className="h-[1px] w-24 bg-gold/50"></div>
                            <span className="text-gold text-[10px] font-bold tracking-[0.5em] uppercase">The Collection</span>
                        </div>
                        <h1 className="font-serif text-8xl md:text-[10rem] tracking-tight leading-[0.8] uppercase text-charcoal">
                            LIQUID <br />
                            <span className="text-gold italic font-light">CRAFT</span>
                        </h1>
                        <p className="text-charcoal/60 text-xl md:text-2xl max-w-2xl leading-relaxed italic border-l-2 border-gold/20 pl-10">
                            "Every bean is a universe. Every roast is a revelation. Welcome to the pinnacle of artisan coffee culture."
                        </p>
                    </div>

                    {/* Dynamic Asymmetric Grid */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-x-20 gap-y-32">
                        {filteredProducts.map((product, index) => (
                            <div
                                key={product.id}
                                className={`group relative space-y-10 transition-all duration-1000 ${isLoaded ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-20'
                                    }`}
                                style={{ transitionDelay: `${index * 150}ms` }}
                            >
                                <div className="relative aspect-[4/5] overflow-hidden bg-cream shadow-sm">
                                    <Image
                                        src={product.image}
                                        alt={product.name}
                                        fill
                                        className="object-cover transition-transform duration-[3s] ease-out group-hover:scale-110 grayscale group-hover:grayscale-0"
                                    />
                                    <div className="absolute inset-0 bg-charcoal/5 group-hover:bg-transparent transition-colors duration-700"></div>

                                    {/* Numbering UI */}
                                    <span className="absolute top-10 left-10 font-serif text-5xl text-charcoal/10 group-hover:text-gold transition-colors duration-700">
                                        0{product.id}
                                    </span>

                                    {/* Quick Action */}
                                    <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity duration-500 bg-charcoal/10 backdrop-blur-[2px]">
                                        <button className="bg-charcoal text-white px-10 py-5 font-bold text-[10px] tracking-[0.3em] uppercase hover:bg-gold hover:text-charcoal transition-all">
                                            Quick View
                                        </button>
                                    </div>
                                </div>

                                <div className="space-y-6">
                                    <div className="flex justify-between items-baseline gap-6">
                                        <h3 className="font-serif text-4xl tracking-tight leading-none text-charcoal group-hover:text-gold transition-colors duration-500 uppercase">
                                            {product.name}
                                        </h3>
                                        <div className="h-[1px] flex-1 bg-gold/10"></div>
                                        <span className="text-lg font-bold text-charcoal tracking-widest shrink-0">{product.price}</span>
                                    </div>

                                    <div className="flex flex-wrap gap-3">
                                        {product.tags.map(tag => (
                                            <span key={tag} className="text-[9px] font-bold tracking-[0.2em] border border-gold/10 px-4 py-1.5 rounded-full text-charcoal/40 group-hover:border-gold group-hover:text-gold transition-all duration-700 uppercase">
                                                {tag}
                                            </span>
                                        ))}
                                    </div>

                                    <p className="text-charcoal/50 text-base leading-relaxed max-w-sm">
                                        {product.description}
                                    </p>
                                </div>
                            </div>
                        ))}
                    </div>

                    {/* Signature Callout Section */}
                    <section className="mt-52 bg-cream p-16 md:p-32 relative overflow-hidden flex flex-col items-center text-center space-y-10 border border-gold/10">
                        <div className="absolute inset-0 pointer-events-none opacity-[0.03] flex items-center justify-center select-none z-0">
                            <h2 className="text-[20vw] font-serif whitespace-nowrap leading-none text-charcoal">
                                BARISTA
                            </h2>
                        </div>

                        <div className="relative z-10 space-y-10">
                            <div className="flex flex-col items-center gap-4">
                                <span className="text-gold text-xs font-bold tracking-[0.5em] uppercase">Artisan Quality</span>
                                <div className="h-20 w-[1px] bg-gold/30"></div>
                            </div>

                            <h3 className="font-serif text-6xl md:text-8xl max-w-4xl leading-[1.1] text-charcoal uppercase">
                                NOT JUST COFFEE. <br />
                                <span className="text-gold italic font-light">POETRY</span> IN A CUP.
                            </h3>

                            <p className="text-charcoal/50 max-w-2xl text-lg md:text-xl font-light leading-relaxed mx-auto italic">
                                "Each roast is a testament to our dedication to the craft. Sourced from single-origin farms and roasted with the heart."
                            </p>

                            <button className="bg-charcoal text-white px-12 py-6 text-[11px] font-bold tracking-[0.4em] uppercase hover:bg-gold hover:text-charcoal transition-all duration-500 shadow-xl">
                                DISCOVER OUR MEMBERSHIP
                            </button>
                        </div>
                    </section>

                </div>
            </div>

        </div>
    );
}
